.headerWrapper {
  width: 1334px;
  height: 76px;
  margin-left: 50%;
  transform: translateX(-50%);
  background-color: #FFF2E3;
  position: fixed;
  z-index: 20;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #B3A99F; }
  .headerWrapper img {
    width: 143px;
    height: 45px; }
  .headerWrapper .headerMenu {
    width: 408px;
    display: flex;
    justify-content: space-between; }
    .headerWrapper .headerMenu a {
      color: black;
      font-family: "Inter";
      font-size: 16px;
      font-weight: 500;
      color: #999188;
      text-decoration: none;
      transition: 0.1s; }
      .headerWrapper .headerMenu a:hover {
